// class applies to select element itself, not a wrapper element
// placeholder selector for use elsewhere
%select-normalized {
  select {
    display: block;
    font-size: 12px;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 1.3;
    padding: .6em 1.4em .5em .8em;
    width: 100%;
    max-width: 100%; // useful when width is set to anything other than 100%
    box-sizing: border-box;
    margin: 0;
    border: 1px solid $gray-10;
    border-radius: 10px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #f5f5f5;
    // note: bg image below uses 2 urls. The first is an svg data uri for the arrow icon, and the second is the gradient. 
    // for the icon, if you want to change the color, be sure to use `%23` instead of `#`, since it's a url. You can also swap in a different svg icon or an external image reference
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23000000%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E');
    background-repeat: no-repeat, repeat;
    background-position: right .7em top 50%, 0 0; // arrow icon position (1em from the right, 50% vertical) , then gradient position
    background-size: .65em auto, 100%; // icon size, then gradient
    cursor: pointer;

    // Hide arrow icon in IE browsers
    &::-ms-expand {
      display: none;
    }

    &:hover, &:focus {
      border-color: #E98300;
      outline: none;
    }

    // &:focus {
    //   border-color: #aaa;
    //   box-shadow: 0 0 1px 2px rgba(199, 90, 18, 0.7);
    //   color: #222; 
    //   outline: none;
    // }

    // Set options to normal weight
    option {
      font-weight: normal;
    }

    // Support for rtl text, explicit support for Arabic and Hebrew
    *[dir="rtl"] &, :root:lang(ar) &, :root:lang(iw) & {
      background-position: left .7em top 50%, 0 0;
      padding: .6em .8em .5em 1.4em;
    }

    // Disabled styles
    &:disabled, &[aria-disabled=true] {
      color: graytext;
      background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22graytext%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
        linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    }

    &:disabled:hover, &[aria-disabled=true] {
      border-color: #aaa;
    }
  }
}